import React, { Component } from 'react';
import { Modal, ModalHeader } from 'reactstrap';
import PropTypes from 'prop-types';
import YouTube from 'react-youtube';

import SpinnerAnimation from 'components/Templates/SpinnerAnimation/SpinnerAnimation';

import './LandingMasVideo.scss';

class LandingMasVideo extends Component {
  constructor(props) {
    super(props);

    this.state = {
      target: null,
    };
  }

  render() {
    const { showModal, backdropClassName, closeModal } = this.props;

    return (
      <Modal
        className="mas-video-container"
        isOpen={showModal}
        size="lg"
        centered
        backdropClassName={backdropClassName}
      >
        <ModalHeader
          toggle={() => {
            try {
              const { target } = this.state;
              if (target) {
                target.pauseVideo();
              }
              closeModal();
            } catch (error) {
              // eslint-disable-next-line
            }
          }}
        />
        <SpinnerAnimation />
        <YouTube
          videoId="mET8hO1nLk0"
          opts={{
            height: '300',
            width: '640',
            playerVars: {
              autoplay: 1,
            },
          }}
          onReady={(e) => {
            try {
              e.target.playVideo();
              this.setState({
                target: e.target,
              });
            } catch (error) {
              // eslint-disable-next-line
            }
          }}
        />
      </Modal>
    );
  }
}

LandingMasVideo.defaultProps = {
  backdropClassName: 'blue-modal-backdrop',
};

LandingMasVideo.propTypes = {
  showModal: PropTypes.bool.isRequired,
  closeModal: PropTypes.func.isRequired,
  backdropClassName: PropTypes.string,
};

export default LandingMasVideo;
