import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import MASBg from 'assets/images/Landing/MAS-BG2.jpg';
import MASLogo from 'assets/images/Landing/MAS_Logo_color.jpg';
import curvedBox from 'assets/images/Landing/curvedBox.png';

// import 'react-lazy-load-image-component/src/effects/opacity.css';

import LandingMasVideo from './LandingMasVideo/LandingMasVideo';

import './LandingMAS.scss';

class LandingMAS extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showModal: false,
    };
  }

  renderInfo() {
    return (
      <div className="mas-info-container">
        <LazyLoadImage
          alt="MASLogo.jpg"
          src={MASLogo}
          // effect="opacity"
        />
        <h2>PolicyPal is a Registered Insurance Broker and Exempt Financial Adviser.</h2>
      </div>
    );
  }

  renderVideo() {
    return (
      <div className="mas-video-container">
        <LazyLoadImage
          alt="MASBg.jpg"
          src={MASBg}
          // effect="opacity"
          className="mas-bg"
        />
        <button
          type="button"
          onClick={() => this.setState({ showModal: true })}
        >
          <LazyLoadImage
            alt="curvedBox.png"
            src={curvedBox}
            // effect="opacity"
            className="curved-box"
          />
        </button>
      </div>
    );
  }

  render() {
    const { showModal } = this.state;

    return (
      <Row className="landing-mas-container">
        <Col className="my-auto" xs={12}>
          <Row className="landing-mas-inner-container info-page-margin" noGutters>
            <Col xs={12} lg={6}>
              {this.renderInfo()}
            </Col>
            <Col className="mt-auto" xs={12} lg={6}>
              {this.renderVideo()}
              <LandingMasVideo
                showModal={showModal}
                closeModal={() => this.setState({ showModal: false })}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }
}

export default LandingMAS;
